import React, { useState } from 'react';
import get from 'lodash/get';

// import API from '../../utils/api';
import './login.scss';

export default (props, context) => {
  const [state, setState] = useState({
    error: false,
    username: get(props, 'location.state.username', ''),
    password: ''
  });
  const inputChanged = ({ target }) => {
    setState({
      ...state,
      [target.name]: target.value,
      error: ''
    });
  }

  const login = async (event) => {
    event.preventDefault();
    const { username, password } = state;
    try {
      if (username === 'admin' && password === 'admin') {
        localStorage.setItem('TOKEN', 'SHANKARREGMI.GITHUB.IO');
        props.history.push('/');
      }
      // const data = await API.post('auth/login', {
      //   username,
      //   password
      // });
      // if (data && data.token) {
      // }
    } catch (error) {
      setState({
        ...state,
        error: error.message
      });
    }
  }
  return (
    <form onSubmit={login}>
      <div className="login-container">
        <div className="text-center">
          <h2 className="heading">Login</h2>
        </div>
        <div className={"text-center error-text " + (!state.error ? 'visibility-hidden': '')}>
          <span>{state.error}</span>
        </div>
        <input type="text" className="form-control col-sm-9" name="username" id="username" value={state.username} onChange={inputChanged} required />
        <input type="password" className="form-control col-sm-9" name="password" id="password" value={state.password} onChange={inputChanged} required />

        <div className="text-center">
          <button type="submit" className="btn login-btn">Login</button>
        </div>
        <div className="text-center signup-text">
          <span>Don't have an account yet? <a href="/register">Create here</a></span>
        </div>
      </div>
    </form>
  );
}
